* {
  padding: 0;
  margin: 0;
  list-style: none;
  font-family: Arial, Helvetica, sans-serif;
}

.App {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: 0.5fr repeat(3, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.side-panel {
  grid-area: 1 / 1 / 5 / 2;
  width: 350px;
  height: 99vh;
  border: 1px solid black;
}

.main-container {
  margin-top: 10px;
  grid-area: 2 / 2 / 5 / 5;
}

.upper-tabs-container {
  grid-area: 1 / 2 / 2 / 6;
  border: 1px solid black;
}

.addtask-input {
  width: 1000px;
}

.block-scroll-wrapper {
  width: calc(100% - 10px);
}

.block-scroll {
  max-height: 700px;
  width: calc(100% + 10px);
  overflow: auto;
  padding-bottom: 20px;
  font-size: 14px;
}

.block-scroll::-webkit-scrollbar {
  width: 5px;
}

.block-scroll::-webkit-scrollbar-thumb {
  border-radius: 10px 0 0 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(112, 111, 111, 0.5);
  background: #79817f;
}

::-webkit-scrollbar {
  width: 0;
}
