.one-task {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0 0 0;
  cursor: grab;
  box-shadow: -1px 7px 13px -7px rgba(138, 136, 136, 0.6);
  height: 50px;
  &:hover {
    box-shadow: -1px 12px 13px -3px rgba(138, 136, 136, 0.6);
  }
}

.like-button {
  background-color: transparent;
  border: 0;
  outline: 0;
  cursor: pointer;
  margin: 0 0 0 10px;
}

.button-star {
  width: 28px;
}

.task-buttons {
  display: flex;
}

.task-button {
  margin: 10px;
  cursor: pointer;
  &:hover {
    transform: scale(1.3);
  }
}

.editTaskInput {
  border: 0;
  font-size: 20px;
  border-bottom: 1px solid black;
  outline: 0;
}

.task-title {
  display: flex;
  align-items: center;
  font-size: 20px;
}

.draggable-item {
  border: 3px solid lightgray;
}